.modal {
  padding: 3rem 0;
  background-color: rgba(13, 16, 49, .8);
  width: 100%;
  height: 100%;
  display: none;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  overflow: auto;
  &.opened {
    display: flex;
  }
  .container {
    //max-width: 328rem;
    @include response-to(vpd) {
      width: 100%;
    }
  }
  .modal-wrapper {
    background-color: $white;
  }
  .close {
    content: '';
    background-image: url(../img/ico_close.svg);
    background-size: contain;
    width: 1rem;
    height: 1rem;
    cursor: pointer;
    position: absolute;
    right: -1rem;
    top: -1rem;
    @include response-to(vpd) {
      background-image: url(../img/ico_close_dark.svg);
      top: 0.5rem;
      right: 0.5rem;
      width: 1rem;
      height: 1rem;
    }
  }
  &.m-login {
    .modal-wrapper {
      position: relative;
      padding: 3rem;
      text-align: center;
      min-width: 20rem;
      -webkit-overflow-scrolling: touch;
      .box {
        border: 1px solid $silver;
        //margin: -12.5rem -15rem;
        padding: 2.5rem 5rem;
      }
      a {
        @extend %t-p;
      }
      @include response-to(vpd) {
        min-width: initial;
        padding: 12px;
        .box {
          //margin: 0;
          padding: 24px;
        }
      }
    }
    label {
      margin-bottom: 1rem;
      font-size: 1.125rem;
      line-height: 2;
      @media (max-width: $lg) {
        margin-bottom: 0.5rem;
        font-size: 1rem;
      }
      &[for="message"] {
        margin-top: 0.5rem;
      }
    }
    input,
    select,
    textarea {
      width: 100%;
      padding: 0.56rem;
      display: block;
      /*background: white;*/
      /*-webkit-appearance: none;*/
      @media (max-width: $lg) {
        padding: 0.3rem;
      }
    }
    form {
      display: flex;
      flex-direction: column;
    }
    h2,
    button {
      align-self: center;
    }
    button {
      margin-top: 1rem;
    }
  }
  &.m-person {
    .modal-wrapper {
      position: relative;
      padding: 5.56rem 8.125rem 5.56rem 0 ;
      display: flex;
      align-items: stretch;
      -webkit-overflow-scrolling: touch;
      @media (max-width: $xl) {
        padding: 3rem;
        margin: auto;
        width: 90%;
      }
      &:before {
        content: '';
        @extend %bg-graydots2;
        background-size: 30%;
        width: calc(100% - 2rem);
        height: calc(100% - 2rem);
        position: absolute;
        left: 1rem;
        top: 1rem;
      }
      @include response-to(vpd) {
        flex-direction: column;
        height: 88vh;
        overflow: auto;
        padding: 20px;
        &:before {
            content: none;
        }
      }
    }
    .aside {
      margin: 0 4rem;
      min-width: 20%;
      position: relative;
      @media (max-width: $sm) {
        margin: 0;
      }
      .photo {
        margin: 0 auto;
        width: 9.375rem;
        height: 9.375rem;
        overflow: hidden;
        border-radius: 50%;
        img {
          width: 100%;
          height: auto;
        }
        @include response-to(vpd) {
          width: 6rem;
          height: 6rem;
          margin: 0 auto;
          img {
              width: 6rem;
          }
        }
      }
      .prev, .next {
        width: 4rem;
        height: auto;
        cursor: pointer;
        position: absolute;
        bottom: 0;
        @include response-to(vpd) {
          width: 3rem;
          bottom: auto;
          top: 50%;
          transform: translateY(-50%);
        }
      }
      .prev {
        left: 0;
      }
      .next {
        right: 0;
      }
    }
    .content {
      // p {
      //     margin-top: 0;
      // }
    }
    h2 {
      margin-top: 1rem;
      margin-bottom: 1rem;
    }
    .role {
      @extend %t-button;
      color: $fern-green;
      text-transform: uppercase;
      margin-bottom: 2rem;
    }
    @include response-to(vpd) {
      .content {
        margin: 0;
      }
      .role {
        margin-bottom: 1rem;
      }
    }
  }
  &.m-enter {
    background-color: white;
    padding-left: 2rem;
    padding-right: 2rem;
    .modal-wrapper {
      display: flex;
      flex-direction: row;
      -webkit-overflow-scrolling: touch;
      .aside {
        padding-top: 2rem;
        img {
          width: 6.5rem;
          height: auto;
        }
      }
      .content {
        flex: 1;
        margin-left: 2rem;
        //margin-right: 22rem;
      }
      h2 {
        margin-bottom: 1.6rem;
        @extend %t-pjumbo;
      }
      .hr {
        height: 2px;
        background-color: $magnetic-grey;
      }
      button {
        align-self: flex-start;
      }
    }
    @include response-to(vpd) {
      padding: 0;
      .container {
        height: 100vh;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
      }
      .modal-wrapper {
        padding-top: 24px;
        padding-bottom: 24px;
        flex-direction: column;
        .aside {
          justify-content: center;
          display: flex;
          img {
            margin-bottom: 24px;
          }
        }
        .content {
          margin-left: 24px;
          margin-right: 24px;
        }
        button {
          align-self: center;
          margin-bottom: 24px;
        }
      }
    }
  }
}
