@import '../_vars.scss';

.strategy {
  &-section {
    padding: 4.8rem 1.5rem 6.4rem;
    text-align: center;
    background-size: 19%;
    @extend %grey;
    @media (max-width: $xl) {
      margin: 0 -0.75rem;
      padding: 3rem 1rem 1rem;
    }
  }
  &-title {
    margin-bottom: 1.5rem;
    font-size: 3.25rem;
    @media (max-width: $xl) {
      font-size: 2.5rem;
    }
  }
  &-text {
    margin: 0 auto 3.4rem;
    max-width: 60%;
    font-size: 1.1rem;
    @media (max-width: $xl) {
      max-width: 80%;
    }
    @media (max-width: $sm) {
      max-width: none;
    }
  }
  &-group {
    display: flex;
    @media (max-width: $xl) {
      flex-direction: column;
    }
  }
  &-item {
    margin: 0 1.5rem;
    flex: 1 1 50%;
    color: #0D1031;
    @media (max-width: $xl) {
      margin-bottom: 2rem;
    }
    @media (max-width: $sm) {
      margin: 0 0 1.5rem;
    }
    &:hover {
      color: #0D1031;
    }
    &-title {
      padding: 0 1rem;
      font-size: 1.5rem;
      line-height: .7;
      display: inline-block;
      background-color: #EFEEF3;
      position: relative;
      z-index: 10;
    }
    &-body {
      padding: 1.75rem;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      top: -.3rem;
      border: 1px solid #D8D8D8;
      @media (max-width: $sm) {
        padding: 1rem 0.5rem;
      }
    }
    &-text {
      margin-bottom: 2.123rem;
      font-size: 1.1rem;
    }
    &-button {
      margin-top: auto;
      padding: 12.5px 50px;
    }
  }
}
