// font vars
$font-face-ptserif: 'PT Serif', serif;
$font-face-kanit: 'Kanit', sans-serif;
$font-face-alice: 'Alice', serif;
$font-face-default: $font-face-ptserif;
$fontsize_base: 8px;

// color vars
$white: #ffffff;
$black: #000000;
$dark-blue-grey: #0d1031; //site text
$sambucus: #0e1032;
$magnetic-grey: #efeef4;
$fern-green: #398b40; //text, b.text, menu hl, play
$wild-forest: #3b8c42; //decoraton
$silver: #d2d1d7; //b.border
$ice-blue: #f2faff; //b.color
$charcoal-grey: #40404a;
$cool-grey: #bab8c0; //tags
$boston-university-red: #c00;

$sm: 36rem; // 576px
$md: 48rem; // 768px
$lg: 62rem; // 992px
$xl: 81.25rem; // 1300px
$xxl: 103.75rem; // 1660px

%grey {
  background: #EFEEF3 url(../../img/sectiongraybg.svg) right top no-repeat;
}

%bg-graydots2 {
    background-image: url(../img/sectionspottedbg2.svg);
    background-position: left top;
    background-repeat: no-repeat;
}
