// typo

%t-menuitem {
    font-family: $font-face-ptserif;
    font-size: 1rem;//4.5rem; //18
    line-height: normal;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
}
%t-button {
    font-family: $font-face-kanit;
    font-size: 15px;//4rem; //16
    line-height: normal;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
}
%t-tag {
    font-family: $font-face-kanit;
    font-size: 2.625rem;//4.5rem; //18
    line-height: 2;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
}
%t-h1 {
    font-family: $font-face-alice;
    font-size: 4rem; //64
    line-height: 0.98;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    @include response-to(vpd) {
        font-size: 36px;
        line-height: 38px;
    }
}
%t-p {
    font-family: $font-face-ptserif;
    line-height: 1.67;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    @include response-to(vpd) {
        font-size: 16px;
        line-height: 28px;
    }
}
%t-pjumbo {
    font-family: $font-face-ptserif;
    font-size: 2.125rem; //34
    line-height: 1.23;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    @include response-to(vpd) {
        font-size: 26px;
        line-height: 38px;
    }
}
%t-h2 {
    font-family: $font-face-alice;
    font-size: 4rem;
    line-height: 1.14;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    @include response-to(vpd) {
        font-size: 36px;
        line-height: 38px;
    }
}
%t-h3 {
    font-family: $font-face-ptserif;
    font-size: 2rem; //28
    line-height: 1.29;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    @include response-to(vpd) {
        font-size: 22px;
        line-height: 32px;
    }
}
%t-psmall {
    font-family: $font-face-ptserif;
    font-size: 4.75rem; //3.75rem; //15
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
}
%t-pbig {
    font-family: $font-face-ptserif;
    font-size: 1.7rem; //28
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
}
%t-h2card {
    font-family: $font-face-alice;
    font-size: 19rem; //76
    line-height: 1;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
}
%t-h3big {
    font-family: $font-face-ptserif;
    font-size: 1.75rem; //28
    line-height: 0.95;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    @include response-to(vpd) {
        font-size: 38px;
        line-height: 36px;
    }
}
