html {
  color: $dark-blue-grey;
}

body {
  margin: 160px 0 0;
  min-height: 100vh;
  min-width: 20rem;
  @media (max-width: $xl) {
    margin-top: 130px;
  }
  @media (max-width: $lg) {
    margin-top: 70px;
  }
}

body.admin-bar {
  header {
    &.fixed {
      .l-sitehead {
        top: 32px;
      }
    }
  }
}

.container {
  max-width: 1210px;
  width: 80vw;
  //max-width: 360rem;
  margin-left: auto;
  margin-right: auto;
  flex: 1;
  @media (max-width: $lg) {
    width: 90vw;
  }
  @media (max-width: $sm) {
    width: 100%;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
  }

}

/* titles */
h1 {
  @extend %t-h1;
}

h2 {
  @extend %t-h2;
}

h3 {
  @extend %t-h3;
}

p, .pagecontent {
  @extend %t-p;
  //color: #0D1031;
}

.jumbo {
  @extend %t-pjumbo;
}

p.tag, p.date {
  @extend %t-tag;
  color: $cool-grey;
  text-transform: uppercase;
}

p + p {
  margin-top: 1.37rem;
}

a {
  text-decoration: none;
  color: rgb(0, 0, 238);
  &:hover {
    text-decoration: none;
    color: rgb(0, 0, 198);
  }
}

button, .button {
  display: inline-block;
  background-color: inherit;
  border: inherit;
  cursor: pointer;
  @extend %t-button;
  text-align: center;
  text-transform: uppercase;
  padding: 11.5px 29px;
  color: $fern-green;
  border: 2px solid $silver;
  transition: all 150ms cubic-bezier(0.19, 1, 0.22, 1);
  &:hover {
    color: $ice-blue;
    border-color: $wild-forest;
    background-color: $wild-forest;
  }
  &.btn {
    color: $ice-blue;
    border-color: $silver;
  }
  &.btn-green {
    border-color: $fern-green;
    background-color: $wild-forest;
    color: $white;
  }
  &.btn-white {
    border-color: $fern-green;
    color: $white;
  }
  &.btn-sm {
    padding: 11.5px 22px;
  }
  @include response-to(vpd) {
    min-width: 0;
    width: 100%;
    max-width: 350px;
  }
  & + button,
  & + .button {
    margin-right: 8px;
    margin-left: 8px;
  }
}

.hr {
  height: 3px;
  min-width: 15rem;
  background-color: $silver;
  margin-bottom: 1rem;
}

.hr-green {
  @extend .hr;
  background-color: $wild-forest;
}

.hr + * {
  margin-top: 1rem !important;
}

input, textarea, select {
  @extend %t-button;
  border: 2px solid $silver;
  text-transform: uppercase;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  // &:invalid {
  //     border: 2px solid $boston-university-red;
  // }
  &.invalid {
    border: 2px solid $boston-university-red;
  }
}

textarea {
  resize: none;
  height: 10.68rem;
}

label {
  @extend %t-menuitem;
  &.invalid {
    color: $boston-university-red;
    input, textarea, select {
      border: 2px solid $boston-university-red;
    }
  }
}

.thin {
  max-width: 60%;
  // p {
  //     margin-top: 0;
  // }
  @include response-to(vpd) {
    max-width: initial;
  }
}

.thin50 {
  @extend .thin;
  max-width: 50%;
}

.thin65 {
  @extend .thin;
  max-width: 65%;
}

.thin70 {
  @extend .thin;
  max-width: 70%;
}

.thin80 {
  @extend .thin;
  max-width: 80%;
}

.thin90 {
  @extend .thin;
  max-width: 90%;
}

/* structure */

/* page head */

main {
  padding-bottom: 4.5rem;
  @media (max-width: $sm) {
    padding-bottom: 2rem;
  }
}
